.search {
  // 头部
  .header {
      height: 1.2rem;
      background-color: #fff;
      i {
          font-size: 0.6rem;
          color: #929292;
      }
      .search {
          width: 6.88rem;
          border-bottom: 0.01rem #929292 solid;
          box-sizing: border-box;
          padding: .1rem .2rem;
          overflow: hidden;
          color: #323233;
          font-size: .35rem;
          line-height: .8rem;
          background-color: #fff;
          input {
              width: 100%;
              color: #323233;
              text-align: left;
              background-color: transparent;
              border: 0;
              outline: none;
              &::-webkit-input-placeholder {
                  color: #929292;
              }
          }
      }
  }
  // 历史记录
  .history {
      margin-top: 0.4rem;
      padding: 0 0.4rem;
      max-height: 4rem;
      overflow: auto;
      .not {
          margin-top: .1rem;
          text-align: center;
      }
      span.tag {
          display: inline-block;
          margin-right: 0.2rem;
          margin-bottom: 0.2rem;
          padding: 0.07rem 0.33rem;
          border-radius: 1rem;
          background-color: rgb(243, 243, 243);
          color: rgb(81, 81, 81);
      }
      >h3 {
          margin-bottom: 0.2rem;
      }
  }
  .history h3 , .most h3{
      font-size: 0.35rem;
      font-weight: bold;
  }
  // 热搜榜
  .most {
      padding: 0.4rem 0.4rem 0;
      .most_list {
        //   padding-top: .25rem;
        padding: .25rem 0 1.5rem 0;
          li {
              display: flex;
              justify-content: space-between;
              align-items: center;
              margin-bottom: 0.4rem;
              .info {
                  display: flex;
                  align-items: center;
                  .order {
                      font-size: 0.4rem;
                      font-weight: 600;
                      color: #aaa;
                  }
                  .song {
                      margin-left: 0.4rem;
                      .song_title {
                          font-size: 0.45rem;
                          margin-bottom: 0.13rem;
                          >img {
                              width: 0.8rem;
                              height: 0.4rem;
                              object-fit: contain;
                              margin-left: .1rem;
                          }
                      }
                      >p {
                          font-size: 0.27rem;
                          color: #aaa;
                      }
                  }
                  // 前三
                  .activateIndex {
                      color: var(--themeColor);
                  }
                  .activateTitle {
                      font-weight: bold;
                  }
              }
              >span {
                  font-size: 0.27rem;
                  color: #aaa;
              }
          }
      }
  }
  // 搜索出来的内容歌曲
  .search_song_list {
      position: absolute;
      left: 0;
      right: 0;
      top: 1.33rem;
      z-index: 999;
      width: 8rem;
      margin: auto;
      background-color: #fff;
      box-shadow: 0 0 0.27rem 0.03rem rgba(0, 0, 0, 0.2);
      li {
          padding-left: 0.33rem;
          border-bottom: 0.01rem solid #eee;
          height: 1.17rem;
          display: flex;
          align-items: center;
          font-size: 0.4rem;
          span {
              margin-left: 0.2rem;
          }
      }
  }
  // 搜索内容详情列表
  .search_list {
      padding: 0 0.35rem;
      li {
          height: 1.3rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          .song {
              width: 100%;
              margin-left: .3rem;
              p {
                  flex: 1;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  white-space: nowrap;
                  font-size: .28rem;
                  &:first-child {
                      color: #333;
                  }
                  &:last-of-type {
                      font-size: 0.25rem;
                  }
              }
          }
          i:last-of-type {
              font-size: .5rem;
          }
      }
  }
}
