.my {
  width: 100%;
  height: 100%;
  background-color: #333;
  min-height: 92vh;
  overflow: hidden;
  // 登陆
  .login {
      overflow: hidden;
      >.title {
          text-align: center;
          font-size: .6rem;
          color: #ffffff;
          margin-top: 4rem;
      }
      >div {
          position: relative;
          display: flex;
          box-sizing: border-box;
          width: 100%;
          padding: 10px 16px;
          overflow: hidden;
          color: #323233;
          font-size: 14px;
          line-height: 24px;
          background-color: #fff;
          >span {
              flex: none;
              box-sizing: border-box;
              width: 4em;
              color: #646566;
              text-align: left;
              word-wrap: break-word;
          }
          >input {
              display: block;
              box-sizing: border-box;
              width: 100%;
              min-width: 0;
              margin: 0;
              padding: 0;
              color: #323233;
              line-height: inherit;
              text-align: left;
              background-color: transparent;
              border: 0;
              resize: none;
              outline: none;
          }
          &::after {
              position: absolute;
              box-sizing: border-box;
              content: ' ';
              pointer-events: none;
              right: 16px;
              bottom: 0;
              left: 16px;
              border-bottom: 1px solid #ebedf0;
              transform: scaleY(.5);
          }
      }
      >button {
          margin: .3rem;
          width: 94%;
          height: 1rem;
          color: #fff;
          background: var(--themeLinear);
          border: 1px solid var(--themeColor);
          border-radius: 999px;
          font-size: .4rem;
          outline: none;
      }
  }
  // 用户展示
  .my_user {
      overflow: hidden;
      position: relative;
      .my_user_delete {
          text-align: center;
          margin-top: 1rem;
          .avatar {
              width: 2rem;
              height: 2rem;
              border-radius: 50%;
              border: 1px solid #545454;
          }
          >.user_nickname {
              font-size: 0.4rem;
              color: var(--themeColor);
              font-weight: 600;
              margin: 0.1rem 0;
          }
          >.user_size {
              font-size: 0.27rem;
              color: #aaa;
              span {
                  &:first-child {
                      margin-right: .1rem;
                  }
                  &:last-child {
                      margin-left: .1rem;
                  }
              }
          }
          .level {
              width: .75rem;
              padding: .2em .5em;
              color: #fff;
              font-size: 10px;
              text-align: center;
              border-radius: .8em;
              background-color: #969799;
              margin: .1rem auto 0;
          }
      }
  }
  .discover_song_head h3 {
      font-weight: inherit !important;
      color: #ffffff !important;
  }
  .song_recommend p {
      color: #ffffff;
  }
  // 关注歌手
  .follow {
      padding: 0 0.4rem;
      >h3 {
          font-size: 0.41rem;
          color: #fff;
          font-weight: inherit;
      }
      ul {
          >li {
              display: flex;
              align-items: center;
              width: 100%;
              height: 2rem;
              border-bottom: 1px solid #eeeeee;
              color: #e5e5e5;
              &:last-of-type {
                  border: none;
              }
              >img {
                  width: 1.5rem;
                  border-radius: 50%;
              }
              >.info {
                  padding: .2rem 0;
                  box-sizing: border-box;
                  margin-left: .2rem;
                  height: 100%;
                  >.text {
                      display: -webkit-box; /*值必须为-webkit-box或者-webkit-inline-box*/
                      -webkit-box-orient: vertical; /*值必须为vertical*/
                      -webkit-line-clamp: 1; /*值为数字，表示一共显示几行*/
                      overflow: hidden;
                  }
                  >section {
                      margin-top: .1rem;
                  }
              }
          }
      }
  }
  .out {
      display: block;
      width: 94%;
      height: 1rem;
      color: #fff;
      background: var(--themeLinear);
      border: 1px solid var(--themeColor);
      border-radius: 999px;
      font-size: .4rem;
      outline: none;
      margin: .1rem auto .3rem;
  }
}
