.rank {
  height: 100vh;
  background: radial-gradient(ellipse, #fff 0, #eee 100%);
  // 头部
  .header {
      background-color: #eee !important;
  }
  // 主体内容
  .rank_sheet {
      padding: 0 0.4rem;
      >h3 {
          padding: 0.19rem 0;
          font-size: 0.43rem;
          color: var(--themeFontColor);
      }
      >a {
          display: flex;
          color: var(--themeFontColor);
          font-size: 0.35rem;
          margin-bottom: 0.27rem;
          >.pic {
              width: 2.61rem;
              height: 2.61rem;
              position: relative;
              img {
                  width: 100%;
                  height: 100%;
                  position: relative;
                  border-radius: 0.13rem;
                  overflow: hidden;
              }
              p {
                  position: absolute;
                  left: 0.13rem;
                  bottom: 0.25rem;
                  font-size: 0.27rem;
                  color: #fff;
                  opacity: 0.9;
              }
          }
          >.msg {
              display: inherit;
              flex-direction: column;
              justify-content: space-around;
              margin-left: 0.2rem;
          }
      }
  }
}
