.dj {
  .header {
      border-bottom: 1px solid #EEEE;
      color: #545454;
  }
  // 轮播图
  .dj_slide {
      margin: 0.27rem 0;
      .slick-list {
          height: 3.47rem;
          border-radius: 0.13rem;
          overflow: hidden;
          margin: 0 0.37rem;
      }
      .slick-dots {
          position: absolute;
          bottom: 8px;
          li {
              width: 6px;
              height: 6px;
              span {
                  display: block;
                  width: 100%;
                  height: 100%;
                  background-color: rgba(0, 0, 0, 0.2);
                  border-radius: 50%;
              }
          }
          .slick-active {
              span {
                  background-color: var(--themeColor);
                  font-size: 14px;
              }
          }
      }
  }
  // 排行榜
  .dj_song {
      height: 4.53rem;
      padding: 0 0.4rem;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      h3 {
          font-size: 0.43rem;
          font-weight: bold;
          color: var(--themeFontColor);
      }
      .song_recommend {
          margin: 0;
          margin-top: .1rem;
      }
  }
  // 电台列表
  .dj_list{
      margin-bottom: 1.5rem;
      .dj_list_item {
          height: 1.57rem;
          display: flex;
          align-items: center;
          justify-content: space-evenly;
          border-bottom: 1px solid #EEEE;
          &:first-child {
              border-top: 1px solid #EEEE;
          }
          >img {
              width: 1.28rem !important;
              height: 1.28rem !important;
              border-radius: 0.13rem;
              overflow: hidden;
          }
          .info {
              width: 6.67rem;
              height: 100%;
              color: #666;
              font-size: 0.35rem;
              display: flex;
              flex-direction: column;
              justify-content: space-evenly;
              >p {
                  color: var(--themeFontColor);
                  white-space: nowrap;
                  overflow: hidden;
                  text-overflow: ellipsis;
              }
              .info_user {
                  font-size: 0.24rem;
                  display: flex;
                  align-items: center;
                  color: var(--themeFontColor);
                  >img {
                      width: 0.4rem;
                      height: 0.4rem;
                      border-radius: 50%;
                      overflow: hidden;
                  }
                  >.name {
                      margin: 0 0.17rem;
                  }
                  >.hot {
                      i {
                          margin-right: .1rem;
                      }
                  }
              }
          }
          >i {
              font-size: 0.61rem;
          }
      }
  }
}
