.home {
  overflow: hidden;
  width: 100%;
  // 首页发现样式
  .discover {
      overflow: hidden;
      width: 100%;
      height: 100%;
      // 轮播图样式
      >.home_slide {
          height: 3.47rem;
          border-radius: 0.13rem;
          overflow: hidden;
          margin: .27rem .37rem;
          .slick-dots {
              position: absolute;
              bottom: 8px;
              li {
                  width: 6px;
                  height: 6px;
                  span{
                      display: block;
                      width: 100%;
                      height: 100%;
                      background-color: rgba(0,0,0,.2);
                      border-radius: 50%;
                  }
                  &.slick-active span{
                      background-color: var(--themeColor);
                  }
              }
          }
      }
      // 首页选项菜单的样式
      >.discover_option {
          display: flex;
          height: 1.95rem;
          padding: 0 0.37rem;
          margin: 0.27rem 0;
          font-size: 0.27rem;
          li {
              flex: 1;
              color: var(--themeFontColor);
              >a,>div {
                  display: flex;
                  flex-direction: column;
                  justify-content: space-around;
                  align-items: center;
                  width: 100%;
                  height: 100%;
              }
              .icon {
                  width: 1.13rem;
                  height: 1.13rem;
                  border-radius: 50%;
                  background: var(--themeLinear);
                  display: inherit;
                  i {
                      margin: auto;
                      font-size: 0.73rem;
                      color: #eee;
                  }
              }
              &:first-child .icon i {
                  font-size: 0.6rem;
              }
          }
      }
      // 首页歌单的样式
      .discover_song_every {
        margin-bottom: 0.8rem;
      }
      >.discover_song {
          // 每日推荐歌单样式
          >.song_every_list {
              display: flex;
              flex-wrap: wrap;
              justify-content: space-around;
              padding: 0.13rem 0.13rem 0;
              >a {
                  margin-bottom: 0.2rem;
              }
          }
      }
  }
}
// 展示轮播图的样式
.slider_show {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2001;
  width: 100%;
  height: 100%;
  opacity: 0;
  background-color: rgba(0,0,0,.9);
  overflow: hidden;
  transition: opacity .5s;
  cursor: grab;
  user-select: none;
  .slider_show_slide {
      width: 100%;
      height: 100%;
      .slick-list{
          position: absolute;
          top: 0;
          bottom: 0;
          width: 100%;
          height: 4.2rem;
          margin: auto;
      }
      .slick-dots {
          position: absolute;
          top: .4rem;
          height: 20px;
          li {
              display: none;
              width: 6px;
              height: 6px;
          }
          .slick-active {
              display: block !important;
              margin: auto;
              div {
                  color: #fff;
                  font-size: 14px;
              }
          }
      }
  }
}

// 显示菜单的内容样式
.menu_mode_fake {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  z-index: 10000;
  opacity: 0;
  background-color: rgba(0, 0, 0,.6);
}
.menu_show{
  position: relative;
  left: 0;
  width: 82%;
  height: 100%;
  background: #ffffff;
  transition: opacity .15s ease-out;
  .drawer_sidebar_bgc {
      height: 4.5rem;
      line-height: 4.5rem;
      background-color: var(--themeColor);
      opacity: .8;
      background-image: url('../images/menu_show_bgc.png');
      background-position: 50%;
      background-size: contain;
      background-repeat: no-repeat;
  }
  .drawer_sidebar_list {
      li {
          display: flex;
          align-items: center;
          width: 100%;
          padding: .3rem;
          height: 1.2rem;
          border-bottom: .005rem solid #545454;
          font-size: .4rem;
          color: #545454;
          p {
              flex: 1;
          }
          i {
              font-size: .4rem;
          }
      }
  }
}
